import React, { useState, useEffect, useRef } from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { MapPin, Star } from 'lucide-react';
import axios from 'axios';
import Header from './Header';
import SearchBar from './SearchBar'; // Import componenta SearchBar separat
import LoginModal from '../login/LoginModal';
import RegisterModal from '../register/RegisterModal';
import Footer from '../pageComponents/Footer';
import './SearchResultsPage.css';

const SearchResultsPage = () => {
    const location = useLocation();
    const navigate = useNavigate();
    const queryParams = new URLSearchParams(location.search);
    const mapRef = useRef(null);
    const mapContainerRef = useRef(null);
    const markersRef = useRef([]); // Referin pentru markere
    const mapInitializedRef = useRef(false); // Flag pentru a urmri dac harta a fost iniializat

    // Stare pentru toate datele necesare paginii
    const [sportsHalls, setSportsHalls] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [hoveredMarker, setHoveredMarker] = useState(null);
    const [mapReady, setMapReady] = useState(false); // Stare pentru a urmri dac harta este gata

    // State pentru header i autentificare
    const [isLoggedIn, setIsLoggedIn] = useState(localStorage.getItem('isLoggedIn') === 'true');
    const [isLoginModalOpen, setIsLoginModalOpen] = useState(false);
    const [isRegisterModalOpen, setIsRegisterModalOpen] = useState(false);

    // Parametrii de cutare din URL
    const [searchParams, setSearchParams] = useState({
        city: queryParams.get('city') || '',
        sport: queryParams.get('sport') || '',
        query: queryParams.get('query') || ''
    });


    const API_URL = process.env.REACT_APP_API_URL || '';

    // Funcie pentru geocodarea adreselor (obinerea coordonatelor din adres)
    const geocodeAddress = async (address, city, id) => {
        if (!address || !city) {
            console.log(`Sala cu ID ${id} nu are adres sau ora complet.`);
            return null;
        }

        try {
            // Folosim Nominatim OpenStreetMap pentru geocodare
            const fullAddress = `${address}, ${city}, Romania`;
            const encodedAddress = encodeURIComponent(fullAddress);
            const response = await axios.get(
                `https://nominatim.openstreetmap.org/search?format=json&q=${encodedAddress}&limit=1`
            );

            // Respectm limitele de utilizare ale API-ului Nominatim
            await new Promise(resolve => setTimeout(resolve, 1000)); // 1 second delay

            if (response.data && response.data.length > 0) {
                const result = response.data[0];
                return {
                    latitude: parseFloat(result.lat),
                    longitude: parseFloat(result.lon)
                };
            } else {
                console.log(`Nu s-au gsit coordonate pentru adresa: ${fullAddress}`);
                return null;
            }
        } catch (error) {
            console.error(`Eroare la geocodarea adresei ${address}, ${city}:`, error);
            return null;
        }
    };

    // ncarc slile de sport folosind noul endpoint de search
    useEffect(() => {
        const fetchSportsHalls = async () => {
            try {
                setLoading(true);

                // Construim URL-ul pentru noul endpoint de search
                const searchURLParams = new URLSearchParams();

                if (searchParams.city) {
                    searchURLParams.append('city', searchParams.city);
                }
                if (searchParams.sport && searchParams.sport !== 'Sport') {
                    searchURLParams.append('sport', searchParams.sport);
                }
                if (searchParams.query) {
                    searchURLParams.append('query', searchParams.query);
                }

                const searchURL = `${API_URL}/sportsHalls/search?${searchURLParams.toString()}`;
                console.log('Fetching from URL:', searchURL);
                console.log('Search parameters:', searchParams);

                // Folosim noul endpoint de search
                const response = await axios.get(searchURL);

                console.log(`Found ${response.data.length} results for search:`, searchParams);
                console.log('Response data:', response.data);

                // Verificm i geocodm adresele care nu au coordonate
                const hallsWithCoordinates = await Promise.all(
                    response.data.map(async (hall) => {
                        // Dac sala are deja coordonate valide, le folosim
                        if (hall.latitude && hall.longitude &&
                            !isNaN(parseFloat(hall.latitude)) &&
                            !isNaN(parseFloat(hall.longitude))) {
                            return hall;
                        }

                        // Dac nu are coordonate dar are adres, ncercm s geocodm
                        if (hall.address && hall.city) {
                            const coordinates = await geocodeAddress(hall.address, hall.city, hall.id);
                            if (coordinates) {
                                return {
                                    ...hall,
                                    latitude: coordinates.latitude,
                                    longitude: coordinates.longitude
                                };
                            }
                        }

                        // Dac geocodarea eueaz sau nu exist adres, returnm sala fr coordonate
                        return hall;
                    })
                );

                setSportsHalls(hallsWithCoordinates);
                setLoading(false);
            } catch (err) {
                console.error('Eroare la ncrcarea slilor:', err);
                console.error('Error details:', err.response?.data || err.message);
                setError('Nu s-au putut ncrca slile de sport.');
                setLoading(false);
            }
        };

        // Doar dac avem cel puin un criteriu de cutare
        if (searchParams.city || searchParams.sport || searchParams.query) {
            console.log('Starting search with params:', searchParams);
            fetchSportsHalls();
        } else {
            console.log('No search criteria provided');
            setLoading(false);
            setSportsHalls([]);
        }
    }, [searchParams, API_URL]);

    // Actualizeaz parametrii cnd se schimb URL-ul
    useEffect(() => {
        const currentParams = new URLSearchParams(location.search);
        const newSearchParams = {
            city: currentParams.get('city') || '',
            sport: currentParams.get('sport') || '',
            query: currentParams.get('query') || ''
        };

        console.log('URL changed, new params:', newSearchParams);
        setSearchParams(newSearchParams);
    }, [location.search]);

    // Funcie separat pentru iniializarea hrii
    const initializeMap = () => {
        if (!mapContainerRef.current || !window.L || mapInitializedRef.current) {
            return;
        }

        try {
            // Curm harta anterioar dac exist
            if (mapRef.current) {
                mapRef.current.remove();
            }

            console.log('Iniializez harta...');

            // Crem harta i adugm tile-urile OpenStreetMap
            const map = window.L.map(mapContainerRef.current, {
                // Opiuni suplimentare pentru stabilitate
                fadeAnimation: false,      // Dezactivm animaiile fade pentru stabilitate
                zoomAnimation: false,      // Dezactivm animaiile zoom pentru stabilitate
                markerZoomAnimation: false // Dezactivm animaiile zoom pentru markere
            }).setView([46.7712, 23.6236], 13); // Centru Cluj-Napoca

            window.L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
                attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
            }).addTo(map);

            mapRef.current = map;
            mapInitializedRef.current = true;

            // Invalidm dimensiunea pentru a fora o recalculare corect
            setTimeout(() => {
                if (mapRef.current) {
                    mapRef.current.invalidateSize();
                    setMapReady(true);
                }
            }, 300);

            console.log('Harta a fost iniializat cu succes');
        } catch (error) {
            console.error('Eroare la iniializarea hrii:', error);
            setError('Nu s-a putut iniializa harta.');
        }
    };

    // Funcie separat pentru adugarea markerelor
    const addMarkersToMap = () => {
        if (!mapRef.current || !sportsHalls.length || !mapReady) {
            return;
        }

        try {
            console.log('Adaug markere pe hart...');

            // Curm markerele existente
            markersRef.current.forEach(marker => {
                if (marker && marker.remove) {
                    marker.remove();
                }
            });
            markersRef.current = [];

            // Adugm markeri noi
            const bounds = [];
            let validMarkers = 0;

            sportsHalls.forEach(hall => {
                // Verificm dac sala are coordonate valide
                if (!hall.latitude || !hall.longitude) {
                    console.log(`Sala ${hall.name} nu are coordonate valide. Nu se adaug marker.`);
                    return; // Skip la aceast sal
                }

                const lat = parseFloat(hall.latitude);
                const lng = parseFloat(hall.longitude);

                // Verificm dac coordonatele sunt numere valide
                if (isNaN(lat) || isNaN(lng)) {
                    console.log(`Sala ${hall.name} are coordonate invalide. Nu se adaug marker.`);
                    return; // Skip la aceast sal
                }

                bounds.push([lat, lng]);
                validMarkers++;

                try {
                    // Crem i adugm markerul la hart
                    const marker = window.L.marker([lat, lng])
                        .addTo(mapRef.current)
                        .bindTooltip(hall.name, { permanent: false, direction: 'top' });

                    marker.hallId = hall.id;

                    marker.on('mouseover', () => {
                        setHoveredMarker(hall.id);
                        marker.openTooltip();
                    });

                    marker.on('mouseout', () => {
                        setHoveredMarker(null);
                        marker.closeTooltip();
                    });

                    marker.on('click', () => {
                        navigate(`/sportsHalls/${hall.id}`);
                    });

                    markersRef.current.push(marker);
                } catch (markerError) {
                    console.error('Eroare la adugarea markerului:', markerError);
                }
            });

            // Ajustm vizualizarea hrii doar dac avem markere valide
            if (bounds.length > 0) {
                try {
                    mapRef.current.fitBounds(bounds, { padding: [30, 30] });
                    console.log(`Au fost adugate ${validMarkers} markere pe hart.`);
                } catch (boundsError) {
                    console.error('Eroare la fitBounds:', boundsError);
                    // Plan de rezerv: centrm pe Cluj-Napoca dac fitBounds eueaz
                    mapRef.current.setView([46.7712, 23.6236], 13);
                }
            } else {
                console.log('Nu exist sli cu coordonate valide pentru afiare pe hart.');
                // Dac nu exist markere, centrm harta pe Cluj-Napoca
                mapRef.current.setView([46.7712, 23.6236], 13);
            }
        } catch (error) {
            console.error('Eroare la adugarea markerelor:', error);
        }
    };

    // Iniializarea hrii cnd componenta se monteaz
    useEffect(() => {
        // Verificm dac DOM este gata
        if (mapContainerRef.current && typeof window !== 'undefined' && window.L) {
            initializeMap();
        }

        // Cleanup cnd componenta se demonteaz
        return () => {
            if (mapRef.current) {
                // Curm markerele
                markersRef.current.forEach(marker => {
                    if (marker && marker.remove) {
                        marker.remove();
                    }
                });
                markersRef.current = [];

                // Apoi eliminm harta
                mapRef.current.remove();
                mapRef.current = null;
                mapInitializedRef.current = false;
                setMapReady(false);
            }
        };
    }, []);

    // Adugm markere cnd harta este gata i datele sunt disponibile
    useEffect(() => {
        if (!loading && !error && sportsHalls.length > 0 && mapReady) {
            addMarkersToMap();
        }
    }, [sportsHalls, loading, error, mapReady]);

    // Actualizm dimensiunea hrii cnd fereastra se redimensioneaz
    useEffect(() => {
        const handleResize = () => {
            if (mapRef.current) {
                mapRef.current.invalidateSize();
            }
        };

        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    const checkLoginStatus = () => {
        setIsLoggedIn(localStorage.getItem('isLoggedIn') === 'true');
    };

    const handleLogout = () => {
        localStorage.removeItem('isLoggedIn');
        localStorage.removeItem('userType');
        localStorage.removeItem('userId');
        localStorage.removeItem('jwtToken');
        setIsLoggedIn(false);
        window.location.href = '/';
    };

    const toggleLoginModal = () => {
        setIsLoginModalOpen(!isLoginModalOpen);
        setIsRegisterModalOpen(false);
    };

    const toggleRegisterModal = () => {
        setIsRegisterModalOpen(!isRegisterModalOpen);
        setIsLoginModalOpen(false);
    };

    // Funcie pentru calcularea ratingului
    const calculateRating = (hall) => {
        if (!hall || !hall.capacity) return 3.0;

        const capacity = hall.capacity || 0;
        let baseRating = 3.5;

        if (capacity > 100) baseRating += 1.2;
        else if (capacity > 50) baseRating += 0.8;
        else if (capacity > 20) baseRating += 0.4;

        const rating = Math.min(5, Math.max(1, baseRating));
        return rating.toFixed(1);
    };

    // Funcie pentru afiarea stelelor de rating
    const renderStars = (rating) => {
        const fullStars = Math.floor(rating);
        const hasHalfStar = rating % 1 >= 0.5;
        const emptyStars = 5 - fullStars - (hasHalfStar ? 1 : 0);

        return (
            <div className="srp-result-stars">
                {[...Array(fullStars)].map((_, i) => (
                    <Star key={`full-${i}`} className="srp-star-full" fill="#FFD700" color="#FFD700" size={14} />
                ))}
                {hasHalfStar && <Star className="srp-star-half" fill="#FFD700" color="#FFD700" size={14} />}
                {[...Array(emptyStars)].map((_, i) => (
                    <Star key={`empty-${i}`} className="srp-star-empty" color="#FFD700" size={14} />
                ))}
                <span className="srp-rating-value">{rating}</span>
            </div>
        );
    };

    // Funcie pentru obinerea URL-ului imaginii
    const getImageUrl = (hall) => {
        if (hall.images && hall.images.length > 0) {
            const coverImage = hall.images.find(img => img.description === 'cover') || hall.images[0];
            return `${API_URL}/images/${coverImage.id}`;
        }
        return 'https://via.placeholder.com/400x250?text=Imagine+Indisponibil';
    };

    // Formatare pre
    const formatPrice = (price) => {
        if (!price) return 'Pre la cerere';
        return `${price} RON/or`;
    };

    // Functie pentru a genera mesajul din titlu
    const getResultsTitle = () => {
        if (!searchParams.city && !searchParams.sport && !searchParams.query) {
            return 'Introducei criterii de cutare pentru a vedea rezultatele';
        }

        let title = 'Sli de sport';

        if (searchParams.query) {
            title += ` "${searchParams.query}"`;
        }

        if (searchParams.city) {
            title += ` n ${searchParams.city}`;
        }

        if (searchParams.sport && searchParams.sport !== 'Sport') {
            title += ` - ${searchParams.sport}`;
        }

        return title;
    };

    return (
        <div className="main-container">
            {/* Header global */}
            <Header
                isLoggedIn={isLoggedIn}
                onLoginClick={toggleLoginModal}
                onRegisterClick={toggleRegisterModal}
                onLogout={handleLogout}
            />

            {/* Search Bar - folosim componenta separat cu styling pentru SearchResults */}
            <div className="srp-search-container">
                <div className="srp-search-wrapper">
                    <SearchBar />
                </div>
            </div>

            {/* Coninut rezultate cutare */}
            <div className="srp-results-content">
                <h1 className="srp-results-title">
                    {getResultsTitle()}
                </h1>

                <div className="srp-results-layout">
                    {/* Lista de sli */}
                    <div className="srp-results-list">
                        {!searchParams.city && !searchParams.sport && !searchParams.query ? (
                            <div className="srp-no-results">
                                V rugm s introducei criterii de cutare: oraul, sportul sau numele slii.
                            </div>
                        ) : loading ? (
                            <div className="srp-loading">Se ncarc slile...</div>
                        ) : error ? (
                            <div className="srp-error">{error}</div>
                        ) : sportsHalls.length === 0 ? (
                            <div className="srp-no-results">Nu s-au gsit sli care s corespund criteriilor de cutare.</div>
                        ) : (
                            sportsHalls.map(hall => (
                                <div
                                    key={hall.id}
                                    className={`srp-result-card ${hoveredMarker === hall.id ? 'srp-result-highlighted' : ''}`}
                                    onMouseEnter={() => setHoveredMarker(hall.id)}
                                    onMouseLeave={() => setHoveredMarker(null)}
                                >
                                    <div className="srp-result-image-container">
                                        <img
                                            src={getImageUrl(hall)}
                                            alt={hall.name}
                                            className="srp-result-image"
                                            onError={(e) => {
                                                e.target.onerror = null;
                                                e.target.src = 'https://via.placeholder.com/400x250?text=Imagine+Indisponibil';
                                            }}
                                        />
                                        {hall.type && (
                                            <div className="srp-result-badge">{hall.type}</div>
                                        )}
                                    </div>
                                    <div className="srp-result-details">
                                        <h2 className="srp-result-name">{hall.name}</h2>
                                        <div className="srp-result-location">
                                            <MapPin size={14} />
                                            <span>{hall.address}, {hall.city}</span>
                                        </div>
                                        <div className="srp-result-rating">
                                            {renderStars(calculateRating(hall))}
                                        </div>
                                        <div className="srp-result-price">
                                            {formatPrice(hall.tariff)}
                                        </div>
                                        {hall.facilities && (
                                            <div className="srp-result-facilities">
                                                {(() => {
                                                    const facilitiesArray = hall.facilities.split(',').map(f => f.trim());
                                                    // Calculm cte faciliti ncap ntr-un spaiu limitat
                                                    const maxVisible = 2; // Reducem la 2 pentru a fi siguri c nu se suprapun
                                                    const visibleFacilities = facilitiesArray.slice(0, maxVisible);
                                                    const remainingCount = facilitiesArray.length - maxVisible;

                                                    return (
                                                        <>
                                                            {visibleFacilities.map((facility, idx) => (
                                                                <span key={idx} className="srp-facility-badge">{facility}</span>
                                                            ))}
                                                            {remainingCount > 0 && (
                                                                <span className="srp-facility-more">+ {remainingCount} mai multe</span>
                                                            )}
                                                        </>
                                                    );
                                                })()}
                                            </div>
                                        )}
                                        <Link to={`/sportsHalls/${hall.id}`} className="srp-result-link">
                                            Vezi detalii
                                        </Link>
                                    </div>
                                </div>
                            ))
                        )}
                    </div>

                    {/* Harta */}
                    <div className="srp-results-map">
                        <div ref={mapContainerRef} className="srp-map-container"></div>
                    </div>
                </div>
            </div>

            {/* Footer Global */}
            <Footer />

            {/* Modals */}
            {!isLoggedIn && (
                <>
                    <LoginModal
                        isOpen={isLoginModalOpen}
                        onClose={toggleLoginModal}
                        onRegisterClick={toggleRegisterModal}
                        onLoginSuccess={checkLoginStatus}
                    />
                    <RegisterModal
                        isOpen={isRegisterModalOpen}
                        onClose={toggleRegisterModal}
                    />
                </>
            )}
        </div>
    );
};

export default SearchResultsPage;