import React, { Component } from 'react';
import { MapPin, ChevronDown } from 'lucide-react';
import './SearchBar.css';

const API_BASE_URL = process.env.REACT_APP_API_URL || '';

class SearchBar extends Component {

    constructor(props) {
        super(props);
        this.state = {
            location: '',
            activity: 'Sport',
            searchQuery: '',
            cities: [],
            loadingCities: true,
            isLocationDropdownOpen: false,
            isActivityDropdownOpen: false,
            // STATE-URI PENTRU AUTOCOMPLETE
            hallSuggestions: [],
            showHallSuggestions: false,
            loadingSuggestions: false
        };

        // Debounce pentru autocomplete
        this.suggestionTimeout = null;
    }

    componentDidMount() {
        this.fetchCities();
        // Adugm event listener pentru click outside
        document.addEventListener('click', this.handleClickOutside);

        // Setm valorile din URL dup ce componentele se ncarc
        this.setValuesFromURL();
    }

    componentWillUnmount() {
        // Curm event listener-ul
        document.removeEventListener('click', this.handleClickOutside);
        // Curm timeout-ul pentru suggestions
        if (this.suggestionTimeout) {
            clearTimeout(this.suggestionTimeout);
        }
    }

    // METOD NOU: Citete parametrii din URL i seteaz starea
    setValuesFromURL = () => {
        const urlParams = new URLSearchParams(window.location.search);
        const cityFromURL = urlParams.get('city');
        const sportFromURL = urlParams.get('sport');
        const queryFromURL = urlParams.get('query');

        // Actualizm starea cu valorile din URL (dac exist)
        this.setState({
            location: cityFromURL || this.state.location,
            activity: sportFromURL || 'Sport',
            searchQuery: queryFromURL || ''
        });

        // Notific componenta printe despre oraul din URL
        if (this.props.onCityChange && cityFromURL) {
            this.props.onCityChange(cityFromURL);
        }
    };

    // Handler pentru click outside pentru a nchide dropdown-urile
    handleClickOutside = (event) => {
        if (!event.target.closest('.search-bar')) {
            this.setState({
                isLocationDropdownOpen: false,
                isActivityDropdownOpen: false,
                showHallSuggestions: false
            });
        }
    };

    // Metod pentru ncrcarea oraelor din baza de date
    fetchCities = async () => {
        this.setState({ loadingCities: true });
        try {
            const response = await fetch(`${API_BASE_URL}/sportsHalls/cities`);
            if (response.ok) {
                const citiesData = await response.json();
                const cities = Array.isArray(citiesData) ? citiesData : [];

                // Verificm dac avem un ora din URL
                const urlParams = new URLSearchParams(window.location.search);
                const cityFromURL = urlParams.get('city');

                // Setm oraul din URL sau primul ora disponibil
                const defaultCity = cityFromURL || (cities.length > 0 ? cities[0] : '');

                this.setState({
                    cities,
                    loadingCities: false,
                    location: defaultCity
                }, () => {
                    // Notific componenta printe despre oraul selectat
                    if (this.props.onCityChange && this.state.location) {
                        this.props.onCityChange(this.state.location);
                    }
                });
            } else {
                console.error('Eroare la ncrcarea oraelor:', response.status);
                this.setState({
                    cities: [],
                    loadingCities: false,
                    location: ''
                });
            }
        } catch (error) {
            console.error('Eroare la ncrcarea oraelor:', error);
            this.setState({
                cities: [],
                loadingCities: false,
                location: ''
            });
        }
    };

    handleLocationChange = (location) => {
        this.setState({
            location: location,
            isLocationDropdownOpen: false
        });

        // Notific componenta printe despre schimbarea oraului
        if (this.props.onCityChange) {
            this.props.onCityChange(location);
        }
    }

    handleActivityChange = (activity) => {
        this.setState({
            activity: activity,
            isActivityDropdownOpen: false
        });
    }

    toggleLocationDropdown = () => {
        this.setState(prevState => ({
            isLocationDropdownOpen: !prevState.isLocationDropdownOpen,
            isActivityDropdownOpen: false,
            showHallSuggestions: false
        }));
    }

    toggleActivityDropdown = () => {
        this.setState(prevState => ({
            isActivityDropdownOpen: !prevState.isActivityDropdownOpen,
            isLocationDropdownOpen: false,
            showHallSuggestions: false
        }));
    }

    handleSearchChange = (e) => {
        const query = e.target.value;
        this.setState({
            searchQuery: query,
            showHallSuggestions: query.length >= 2 // Arat sugestiile doar pentru query-uri de minim 2 caractere
        });

        // Debounce pentru autocomplete - ateapt 300ms dup ce utilizatorul se oprete din tastare
        if (this.suggestionTimeout) {
            clearTimeout(this.suggestionTimeout);
        }

        if (query.length >= 2) {
            this.suggestionTimeout = setTimeout(() => {
                this.fetchHallSuggestions(query);
            }, 300);
        } else {
            this.setState({
                hallSuggestions: [],
                showHallSuggestions: false
            });
        }
    }

    // Metod pentru a ncrca sugestiile de nume sli
    fetchHallSuggestions = async (query) => {
        this.setState({ loadingSuggestions: true });
        try {
            const response = await fetch(`${API_BASE_URL}/sportsHalls/names/suggestions?query=${encodeURIComponent(query)}`);
            if (response.ok) {
                const suggestions = await response.json();
                this.setState({
                    hallSuggestions: suggestions,
                    loadingSuggestions: false
                });
            } else {
                console.error('Eroare la ncrcarea sugestiilor:', response.status);
                this.setState({
                    hallSuggestions: [],
                    loadingSuggestions: false
                });
            }
        } catch (error) {
            console.error('Eroare la ncrcarea sugestiilor:', error);
            this.setState({
                hallSuggestions: [],
                loadingSuggestions: false
            });
        }
    };

    // Handler pentru selectarea unei sugestii
    handleSuggestionSelect = (suggestion) => {
        this.setState({
            searchQuery: suggestion,
            showHallSuggestions: false,
            hallSuggestions: []
        });
    };

    handleSearch = (e) => {
        e.preventDefault();

        const searchParams = new URLSearchParams();

        // Construim parametrii de cutare n funcie de ce a completat utilizatorul

        // Adugm numele slii dac exist
        if (this.state.searchQuery && this.state.searchQuery.trim().length > 0) {
            searchParams.append('query', this.state.searchQuery.trim());
        }

        // Adugm oraul dac este selectat
        if (this.state.location && this.state.location.trim().length > 0) {
            searchParams.append('city', this.state.location.trim());
        }

        // Adugm sportul (doar dac nu este "Sport" - valoarea default)
        if (this.state.activity && this.state.activity !== 'Sport') {
            searchParams.append('sport', this.state.activity);
        }

        // Validm c avem cel puin un criteriu de cutare
        if (!this.state.searchQuery?.trim() && !this.state.location?.trim() && this.state.activity === 'Sport') {
            alert('V rugm s introducei cel puin un criteriu de cutare: numele slii, oraul sau sportul.');
            return;
        }

        // Redirecionm ctre pagina de search cu noii parametri (comportamentul original)
        window.location.href = `/search?${searchParams.toString()}`;
    }

    render() {
        const { cities, loadingCities, hallSuggestions, showHallSuggestions, loadingSuggestions } = this.state;
        const activities = ['Sport', 'Inot', 'Tenis', 'Fotbal', 'Baschet', 'Handbal', 'Volei', 'Polivalenta'];

        return (
            <div className="search-container">
                <form onSubmit={this.handleSearch} className="search-bar">
                    <div className="input-group">
                        <MapPin className="icon" />
                        <div className="custom-select">
                            <div
                                className="selected-option"
                                onClick={this.toggleLocationDropdown}
                            >
                                {loadingCities
                                    ? 'Se ncarc oraele...'
                                    : this.state.location || 'Selecteaz oraul'
                                }
                                <ChevronDown className="icon-small" />
                            </div>
                            {this.state.isLocationDropdownOpen && !loadingCities && (
                                <div className="options-list">
                                    {cities.length > 0 ? (
                                        cities.map((city) => (
                                            <div
                                                key={city}
                                                className="option"
                                                onClick={() => this.handleLocationChange(city)}
                                            >
                                                {city}
                                            </div>
                                        ))
                                    ) : (
                                        <div className="option disabled">
                                            Nu exist orae disponibile
                                        </div>
                                    )}
                                </div>
                            )}
                        </div>
                    </div>

                    <div className="input-group">
                        <div className="custom-select">
                            <div
                                className="selected-option"
                                onClick={this.toggleActivityDropdown}
                            >
                                {this.state.activity}
                                <ChevronDown className="icon-small" />
                            </div>
                            {this.state.isActivityDropdownOpen && (
                                <div className="options-list">
                                    {activities.map((activity) => (
                                        <div
                                            key={activity}
                                            className="option"
                                            onClick={() => this.handleActivityChange(activity)}
                                        >
                                            {activity}
                                        </div>
                                    ))}
                                </div>
                            )}
                        </div>
                    </div>

                    <div className="input-group search-input-group">
                        <input
                            type="text"
                            value={this.state.searchQuery}
                            onChange={this.handleSearchChange}
                            placeholder="Denumire sala de sport"
                            className="search-input"
                            autoComplete="off"
                        />

                        {/* Dropdown pentru sugestiile de sli */}
                        {showHallSuggestions && (
                            <div className="suggestions-list">
                                {loadingSuggestions ? (
                                    <div className="suggestion loading">Se ncarc sugestiile...</div>
                                ) : hallSuggestions.length > 0 ? (
                                    hallSuggestions.map((suggestion, index) => (
                                        <div
                                            key={index}
                                            className="suggestion"
                                            onClick={() => this.handleSuggestionSelect(suggestion)}
                                        >
                                            {suggestion}
                                        </div>
                                    ))
                                ) : (
                                    <div className="suggestion no-results">
                                        Nu s-au gsit sli cu acest nume
                                    </div>
                                )}
                            </div>
                        )}
                    </div>

                    <button
                        type="submit"
                        className="search-button"
                        disabled={loadingCities}
                    >
                        CAUT
                    </button>
                </form>
            </div>
        );
    }
}

export default SearchBar;