import React, { useEffect, useState } from 'react';
import './SportsHallMap.css';

const SportsHallMap = ({ address, city, county }) => {
    const [mapUrl, setMapUrl] = useState('');
    const [isLoading, setIsLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        if (!address || !city) {
            setError('Adresa sau oraul lipsete');
            setIsLoading(false);
            return;
        }

        try {
            // Construim adresa complet pentru geocoding
            const fullAddress = `${address}, ${city}${county ? ', ' + county : ''}, Romania`;

            // Folosim Nominatim pentru a obine coordonatele (lat/lon) pe baza adresei
            // Nominatim este serviciul OpenStreetMap pentru geocoding
            fetch(`https://nominatim.openstreetmap.org/search?format=json&q=${encodeURIComponent(fullAddress)}`)
                .then(response => response.json())
                .then(data => {
                    if (data && data.length > 0) {
                        // Avem coordonate
                        const lat = data[0].lat;
                        const lon = data[0].lon;

                        // Crem URL-ul pentru OpenStreetMap cu marcaj la coordonatele specifice
                        const openStreetMapUrl = `https://www.openstreetmap.org/export/embed.html?bbox=${parseFloat(lon)-0.01},${parseFloat(lat)-0.01},${parseFloat(lon)+0.01},${parseFloat(lat)+0.01}&layer=mapnik&marker=${lat},${lon}`;

                        setMapUrl(openStreetMapUrl);
                    } else {
                        // Dac nu putem obine coordonate exacte, folosim o vizualizare general a oraului
                        const openStreetMapUrl = `https://www.openstreetmap.org/export/embed.html?bbox=20.0,44.0,30.0,48.0&layer=mapnik&query=${encodeURIComponent(fullAddress)}`;
                        setMapUrl(openStreetMapUrl);
                    }
                    setIsLoading(false);
                })
                .catch(err => {
                    console.error("Eroare n geocoding:", err);
                    // Fallback la o hart general
                    const openStreetMapUrl = `https://www.openstreetmap.org/export/embed.html?bbox=20.0,44.0,30.0,48.0&layer=mapnik&query=${encodeURIComponent(fullAddress)}`;
                    setMapUrl(openStreetMapUrl);
                    setIsLoading(false);
                });
        } catch (err) {
            console.error("Eroare general:", err);
            setError('Nu s-a putut ncrca harta');
            setIsLoading(false);
        }
    }, [address, city, county]);

    if (isLoading) {
        return <div className="detail-map-loading">Se ncarc harta...</div>;
    }

    if (error) {
        return <div className="detail-map-error">{error}</div>;
    }

    return (
        <div className="detail-map-container">
            <h2 className="detail-section-title">Locaie</h2>
            <div className="detail-map-wrapper">
                <iframe
                    src={mapUrl}
                    width="100%"
                    height="300"
                    frameBorder="0"
                    style={{ border: 0 }}
                    allowFullScreen
                    aria-hidden="false"
                    tabIndex="0"
                    title="Locaie sal de sport"
                    className="detail-map-iframe"
                    loading="lazy"
                />
            </div>
            <div className="detail-map-address">
                <p className="detail-map-address-text">
                    {address}, {city}{county ? `, ${county}` : ''}
                </p>
                <a
                    href={`https://www.google.com/maps/search/?api=1&query=${encodeURIComponent(`${address}, ${city}, Romania`)}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="detail-map-directions"
                >
                    Vezi indicaii rutiere
                </a>
            </div>
        </div>
    );
};

export default SportsHallMap;