import React, { useState } from 'react';

const SecuritySettings = () => {
    const [isChangingPassword, setIsChangingPassword] = useState(false);
    const [passwordData, setPasswordData] = useState({
        currentPassword: '',
        newPassword: '',
        confirmPassword: ''
    });
    const [errors, setErrors] = useState({});
    const [loading, setLoading] = useState(false);

    const handleChangePassword = () => {
        setIsChangingPassword(true);
        setPasswordData({
            currentPassword: '',
            newPassword: '',
            confirmPassword: ''
        });
        setErrors({});
    };

    const API_BASE_URL = process.env.REACT_APP_API_URL || '';


    const handlePasswordInputChange = (e) => {
        const { name, value } = e.target;
        setPasswordData(prev => ({
            ...prev,
            [name]: value
        }));

        // Clear error for this field when user starts typing
        if (errors[name]) {
            setErrors(prev => ({
                ...prev,
                [name]: ''
            }));
        }
    };

    const validatePasswordForm = () => {
        const newErrors = {};

        if (!passwordData.currentPassword) {
            newErrors.currentPassword = 'Parola curent este obligatorie';
        }

        if (!passwordData.newPassword) {
            newErrors.newPassword = 'Parola nou este obligatorie';
        } else if (passwordData.newPassword.length < 6) {
            newErrors.newPassword = 'Parola nou trebuie s aib cel puin 6 caractere';
        }

        if (!passwordData.confirmPassword) {
            newErrors.confirmPassword = 'Confirmarea parolei este obligatorie';
        } else if (passwordData.newPassword !== passwordData.confirmPassword) {
            newErrors.confirmPassword = 'Parolele nu se potrivesc';
        }

        if (passwordData.currentPassword === passwordData.newPassword) {
            newErrors.newPassword = 'Parola nou trebuie s fie diferit de cea curent';
        }

        setErrors(newErrors);
        return Object.keys(newErrors).length === 0;
    };

    const handleSavePassword = async () => {
        if (!validatePasswordForm()) {
            return;
        }

        setLoading(true);

        try {
            // Get the current user ID from localStorage or context
            const token = localStorage.getItem('jwtToken');
            if (!token) {
                throw new Error('Nu eti autentificat');
            }

            // Decode the token to get user info (you might need to implement this differently based on your auth setup)
            const tokenPayload = JSON.parse(atob(token.split('.')[1]));
            const userEmail = tokenPayload.sub;

            // Find user by email to get the ID
            const usersResponse = await fetch(`${API_BASE_URL}/users`, {
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json'
                }
            });

            if (!usersResponse.ok) {
                throw new Error('Eroare la obinerea datelor utilizatorului');
            }

            const users = await usersResponse.json();
            const currentUser = users.find(user => user.email === userEmail);

            if (!currentUser) {
                throw new Error('Utilizatorul nu a fost gsit');
            }

            // Call the change password endpoint
            const response = await fetch(`${API_BASE_URL}/users/${currentUser.id}/change-password`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    currentPassword: passwordData.currentPassword,
                    newPassword: passwordData.newPassword
                })
            });

            if (!response.ok) {
                const errorData = await response.json();
                throw new Error(errorData.message || 'Eroare la schimbarea parolei');
            }

            // Success
            alert('Parola a fost schimbat cu succes!');
            setIsChangingPassword(false);
            setPasswordData({
                currentPassword: '',
                newPassword: '',
                confirmPassword: ''
            });

        } catch (error) {
            console.error('Eroare la schimbarea parolei:', error);
            setErrors({
                general: error.message || 'A aprut o eroare la schimbarea parolei'
            });
        } finally {
            setLoading(false);
        }
    };

    const handleCancelPasswordChange = () => {
        setIsChangingPassword(false);
        setPasswordData({
            currentPassword: '',
            newPassword: '',
            confirmPassword: ''
        });
        setErrors({});
    };

    const handleTwoFactorAuth = () => {
        // TODO: Implementeaz logica pentru autentificarea n dou pai
        console.log('Configureaz autentificarea n dou pai');
    };

    const handleDeleteAccount = () => {
        // TODO: Implementeaz logica pentru tergerea contului
        if (window.confirm('Eti sigur c vrei s tergi permanent contul? Aceast aciune nu poate fi anulat.')) {
            console.log('terge contul');
            // Aici ar trebui s faci un request ctre backend pentru tergerea contului
        }
    };

    return (
        <div className="profile-section">
            <div className="section-header">
                <h2>Securitate</h2>
            </div>

            <div className="security-section">
                <div className="security-item">
                    <div className="security-info">
                        <h3>Schimb parola</h3>
                        <p>Actualizeaz-i parola pentru a menine contul securizat</p>
                    </div>
                    {!isChangingPassword ? (
                        <button
                            className="security-button"
                            onClick={handleChangePassword}
                        >
                            Schimb parola
                        </button>
                    ) : (
                        <div style={{ width: '100%' }}>
                            <div className="personal-data-grid" style={{ marginTop: '1rem' }}>
                                <div className="data-field">
                                    <label>Parola curent</label>
                                    <input
                                        type="password"
                                        name="currentPassword"
                                        className={`edit-input ${errors.currentPassword ? 'error' : ''}`}
                                        value={passwordData.currentPassword}
                                        onChange={handlePasswordInputChange}
                                        placeholder="Introdu parola curent"
                                    />
                                    {errors.currentPassword && (
                                        <span style={{ color: '#ef4444', fontSize: '0.8rem' }}>
                                            {errors.currentPassword}
                                        </span>
                                    )}
                                </div>

                                <div className="data-field">
                                    <label>Parola nou</label>
                                    <input
                                        type="password"
                                        name="newPassword"
                                        className={`edit-input ${errors.newPassword ? 'error' : ''}`}
                                        value={passwordData.newPassword}
                                        onChange={handlePasswordInputChange}
                                        placeholder="Introdu parola nou"
                                    />
                                    {errors.newPassword && (
                                        <span style={{ color: '#ef4444', fontSize: '0.8rem' }}>
                                            {errors.newPassword}
                                        </span>
                                    )}
                                </div>

                                <div className="data-field" style={{ gridColumn: 'span 2' }}>
                                    <label>Confirm parola nou</label>
                                    <input
                                        type="password"
                                        name="confirmPassword"
                                        className={`edit-input ${errors.confirmPassword ? 'error' : ''}`}
                                        value={passwordData.confirmPassword}
                                        onChange={handlePasswordInputChange}
                                        placeholder="Confirm parola nou"
                                    />
                                    {errors.confirmPassword && (
                                        <span style={{ color: '#ef4444', fontSize: '0.8rem' }}>
                                            {errors.confirmPassword}
                                        </span>
                                    )}
                                </div>
                            </div>

                            {errors.general && (
                                <div style={{
                                    color: '#ef4444',
                                    fontSize: '0.9rem',
                                    marginTop: '1rem',
                                    textAlign: 'center'
                                }}>
                                    {errors.general}
                                </div>
                            )}

                            <div className="edit-actions" style={{ marginTop: '1rem' }}>
                                <button
                                    className="save-button"
                                    onClick={handleSavePassword}
                                    disabled={loading}
                                >
                                    {loading ? 'Se salveaz...' : 'Salveaz parola'}
                                </button>
                                <button
                                    className="cancel-button"
                                    onClick={handleCancelPasswordChange}
                                    disabled={loading}
                                >
                                    Anuleaz
                                </button>
                            </div>
                        </div>
                    )}
                </div>

                <div className="security-item">
                    <div className="security-info">
                        <h3>Autentificare n dou pai</h3>
                        <p>Adaug un nivel suplimentar de securitate la contul tu</p>
                    </div>
                    <button
                        className="security-button"
                        onClick={handleTwoFactorAuth}
                    >
                        Configureaz
                    </button>
                </div>

                <div className="security-item danger">
                    <div className="security-info">
                        <h3>terge contul</h3>
                        <p>terge permanent contul i toate datele asociate</p>
                    </div>
                    <button
                        className="security-button danger"
                        onClick={handleDeleteAccount}
                    >
                        terge contul
                    </button>
                </div>
            </div>
        </div>
    );
};

export default SecuritySettings;