import React, { useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import './StripePaymentSuccessPage.css';

const StripePaymentSuccessPage = () => {
    const navigate = useNavigate();
    const location = useLocation();

    const {
        reservations: reservationData = [],
        totalAmount = 0,
        paymentMethod = 'N/A',
        paymentDetails = {}
    } = location.state || {};

    useEffect(() => {
        if (reservationData.length === 0) {
            alert('Detaliile rezervrii nu au fost gsite. Redirectare...');
            navigate('/');
        }
    }, [reservationData, navigate]);

    if (reservationData.length === 0) {
        return (
            <div className="payment-success-page">
                <div className="success-container">
                    <div className="loading-section">Se ncarc...</div>
                </div>
            </div>
        );
    }

    const displayTransactionId = paymentMethod === 'card' ?
        (paymentDetails?.stripePaymentIntentId || paymentDetails?.transactionId) :
        paymentDetails?.paymentId;

    const currentDate = new Date().toLocaleDateString('ro-RO', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
    });

    return (
        <div className="payment-success-page">
            <div className="success-container">
                <div className="document-header">
                    <div className="company-header">
                        <h1 className="company-name">TRAINON</h1>
                        <p className="company-subtitle">Platform Rezervri Sportive</p>
                    </div>
                    <div className="document-info">
                        <p className="document-type">CONFIRMAREA REZERVRII</p>
                        <p className="document-date">Data emiterii: {currentDate}</p>
                    </div>
                </div>

                <div className="status-section">
                    <div className="status-indicator success">
                        <div className="status-icon"></div>
                        <div className="status-content">
                            <h2 className="status-title">Rezervare Confirmat</h2>
                            <p className="status-message">
                                {paymentMethod === 'cash' ?
                                    'Rezervarea dumneavoastr a fost nregistrat cu succes n sistemul nostru.' :
                                    'Plata a fost procesat cu succes i rezervarea este confirmat.'
                                }
                            </p>
                        </div>
                    </div>
                </div>

                <div className="content-section">
                    <div className="section-block">
                        <h3 className="section-title">Detalii Rezervare</h3>
                        <div className="details-table">
                            <div className="table-header">
                                <div className="col-header">Sala de Sport</div>
                                <div className="col-header">Data</div>
                                <div className="col-header">Interval Orar</div>
                                <div className="col-header">Tarif</div>
                            </div>
                            {reservationData.map((reservation, index) => (
                                <div key={index} className="table-row">
                                    <div className="table-cell">{reservation.hallName}</div>
                                    <div className="table-cell">{reservation.dateFormatted}</div>
                                    <div className="table-cell">{reservation.timeSlot}</div>
                                    <div className="table-cell">{(totalAmount / reservationData.length).toFixed(2)} RON</div>
                                </div>
                            ))}
                            <div className="table-footer">
                                <div className="total-label">Total de plat:</div>
                                <div className="total-amount">{totalAmount.toFixed(2)} RON</div>
                            </div>
                        </div>
                    </div>

                    <div className="section-block">
                        <h3 className="section-title">Informaii Tranzacie</h3>
                        <div className="info-grid">
                            <div className="info-row">
                                <span className="info-label">Metod de plat:</span>
                                <span className="info-value">
                                    {paymentMethod === 'card' ? 'Card de Credit/Debit' :
                                        paymentMethod === 'cash' ? 'Plat Numerar' : paymentMethod}
                                </span>
                            </div>
                            {displayTransactionId && (
                                <div className="info-row">
                                    <span className="info-label">ID Tranzacie:</span>
                                    <span className="info-value transaction-id">{displayTransactionId}</span>
                                </div>
                            )}
                            {paymentDetails?.reservationIds && (
                                <div className="info-row">
                                    <span className="info-label">ID-uri Rezervri:</span>
                                    <span className="info-value">{paymentDetails.reservationIds.join(', ')}</span>
                                </div>
                            )}
                            <div className="info-row">
                                <span className="info-label">Status:</span>
                                <span className="info-value status-confirmed">Confirmat</span>
                            </div>
                        </div>
                    </div>

                    <div className="section-block">
                        <h3 className="section-title">Instruciuni Importante</h3>
                        <div className="instructions-list">
                            <div className="instruction-item">
                                <span className="instruction-number">1</span>
                                <span className="instruction-text">Prezentai-v cu 15 minute nainte de ora rezervat</span>
                            </div>
                            <div className="instruction-item">
                                <span className="instruction-number">2</span>
                                <span className="instruction-text">Asigurai-v c avei echipamentul sportiv necesar</span>
                            </div>
                            <div className="instruction-item">
                                <span className="instruction-number">3</span>
                                <span className="instruction-text">ntrzierile mai mari de 15 minute pot rezulta n anularea rezervrii</span>
                            </div>
                            {paymentMethod === 'cash' && (
                                <div className="instruction-item cash-note">
                                    <span className="instruction-number">!</span>
                                    <span className="instruction-text">
                                        <strong>Atenie:</strong> Plata se va efectua la faa locului nainte de nceperea activitii
                                    </span>
                                </div>
                            )}
                        </div>
                    </div>

                    <div className="section-block">
                        <h3 className="section-title">Contact i Suport</h3>
                        <div className="contact-info">
                            <p>Pentru modificri, anulri sau ntrebri, v rugm s ne contactai prin:</p>
                            <ul className="contact-list">
                                <li>Platforma online TrainOn</li>
                                <li>Seciunea "Contul Meu"  "Rezervrile Mele"</li>
                                <li>Program de funcionare: Luni - Duminic, 08:00 - 22:00</li>
                            </ul>
                        </div>
                    </div>
                </div>

                <div className="action-section no-print">
                    <button onClick={() => navigate('/')} className="btn btn-primary">
                        napoi la Pagina Principal
                    </button>
                    <button onClick={() => window.print()} className="btn btn-secondary">
                        Printeaz Confirmarea
                    </button>
                </div>

                <div className="document-footer">
                    <div className="footer-line"></div>
                    <p className="footer-text">
                        Acest document a fost generat automat de sistemul TrainOn n data de {currentDate}
                    </p>
                    <p className="footer-note">
                        Pstrai aceast confirmare pentru evidenele dumneavoastr
                    </p>
                </div>
            </div>
        </div>
    );
};

export default StripePaymentSuccessPage;