import React, { Component } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { Camera, X, ImagePlus, ChevronLeft, ChevronRight, Trash2 } from 'lucide-react';
import './EditHallPage.css';

const COUNTIES = [
    'Alba', 'Arad', 'Arge', 'Bacu', 'Bihor', 'Bistria-Nsud', 'Botoani',
    'Brila', 'Braov', 'Bucureti', 'Buzu', 'Clrai', 'Cara-Severin',
    'Cluj', 'Constana', 'Covasna', 'Dmbovia', 'Dolj', 'Galai', 'Giurgiu',
    'Gorj', 'Harghita', 'Hunedoara', 'Ialomia', 'Iai', 'Ilfov', 'Maramure',
    'Mehedini', 'Mure', 'Neam', 'Olt', 'Prahova', 'Slaj', 'Satu Mare',
    'Sibiu', 'Suceava', 'Teleorman', 'Timi', 'Tulcea', 'Vlcea', 'Vaslui',
    'Vrancea'
].sort();

const IMAGE_TYPES = [
    {
        type: 'cover',
        label: 'Imagine Copert',
        description: 'Imagine principal a slii (max. 10MB)',
        max: 1
    },
    {
        type: 'exterior',
        label: 'Imagine Exterior',
        description: 'Imagine cu faada slii (max. 10MB)',
        max: 1
    },
    {
        type: 'interior',
        label: 'Imagini Interior',
        description: 'Imagini din interiorul slii (max. 10MB/imagine)',
        max: 4
    },
    {
        type: 'locker',
        label: 'Imagini Vestiare',
        description: 'Imagini cu vestiare (max. 10MB/imagine)',
        max: 2
    }
];


// Wrapper pentru a folosi hooks in component class
function withRouter(Component) {
    function ComponentWithRouterProp(props) {
        let params = useParams();
        let navigate = useNavigate();
        return <Component {...props} params={params} navigate={navigate} />;
    }
    return ComponentWithRouterProp;
}

class EditHallPage extends Component {
    constructor(props) {
        super(props);
        this.API_BASE_URL = process.env.REACT_APP_API_URL || '';
        this.state = {
            hallId: props.params.id,
            hallData: {
                name: '',
                county: '',
                city: '',
                address: '',
                capacity: '',
                type: '',
                tariff: '',
                description: '',
                facilities: ''
            },
            existingImages: {
                cover: [],
                exterior: [],
                interior: [],
                locker: []
            },
            newImages: {
                cover: [],
                exterior: [],
                interior: [],
                locker: []
            },
            imageIdsToDelete: [],
            activeImageType: null,
            currentImageIndex: 0,
            isImageViewerOpen: false,
            isLoading: true,
            isSubmitting: false,
            submitError: null,
            submitSuccess: false
        };
    }

    async componentDidMount() {
        await this.fetchHallData();
    }

    fetchHallData = async () => {
        try {
            const { hallId } = this.state;
            const token = localStorage.getItem('jwtToken');

            if (!token) {
                this.setState({
                    submitError: 'Nu suntei autentificat. V rugm s v autentificai pentru a edita sala.',
                    isLoading: false
                });
                return;
            }

            const response = await fetch(`${this.API_BASE_URL}/sportsHalls/${hallId}`, {
                method: 'GET',
                headers: {
                    Authorization: `Bearer ${token}`
                }
            });

            if (!response.ok) {
                throw new Error(`Eroare la ncrcarea slii: ${response.status}`);
            }

            const hallData = await response.json();
            console.log('Hall data loaded:', hallData);

            // Organizarea imaginilor pe categorii
            const existingImages = {
                cover: [],
                exterior: [],
                interior: [],
                locker: []
            };

            if (hallData.images && hallData.images.length > 0) {
                hallData.images.forEach(img => {
                    const type = img.description;
                    if (existingImages[type]) {
                        existingImages[type].push({
                            id: img.id,
                            url: `${this.API_BASE_URL}/images/${img.id}`,
                            type: img.description
                        });
                    }
                });
            }

            // Actualizam starea cu datele incarcate
            this.setState({
                hallData: {
                    name: hallData.name || '',
                    county: hallData.county || '',
                    city: hallData.city || '',
                    address: hallData.address || '',
                    capacity: hallData.capacity || '',
                    type: hallData.type || '',
                    tariff: hallData.tariff || '',
                    description: hallData.description || '',
                    facilities: hallData.facilities || ''
                },
                existingImages,
                isLoading: false
            });
        } catch (error) {
            console.error('Error fetching hall data:', error);
            this.setState({
                submitError: 'Eroare la incarcarea datelor salii',
                isLoading: false
            });
        }
    };

    handleInputChange = (e) => {
        const { name, value } = e.target;
        this.setState(prevState => ({
            hallData: {
                ...prevState.hallData,
                [name]: value
            }
        }));
    };

    handleFileChange = (e, imageType) => {
        const newFiles = Array.from(e.target.files);

        this.setState(prevState => {
            const currentCount = prevState.newImages[imageType].length + prevState.existingImages[imageType].length;
            const typeConfig = IMAGE_TYPES.find(type => type.type === imageType);
            const maxAllowed = typeConfig.max;

            const filesToAdd = newFiles.slice(0, maxAllowed - currentCount);

            return {
                newImages: {
                    ...prevState.newImages,
                    [imageType]: [
                        ...prevState.newImages[imageType],
                        ...filesToAdd.map(file => ({
                            file,
                            preview: URL.createObjectURL(file),
                            type: imageType
                        }))
                    ]
                }
            };
        });
    };

    removeExistingImage = (imageType, index) => {
        this.setState(prevState => {
            const imageToRemove = prevState.existingImages[imageType][index];
            return {
                existingImages: {
                    ...prevState.existingImages,
                    [imageType]: prevState.existingImages[imageType].filter((_, i) => i !== index)
                },
                imageIdsToDelete: [...prevState.imageIdsToDelete, imageToRemove.id]
            };
        });
    };

    removeNewImage = (imageType, index) => {
        this.setState(prevState => ({
            newImages: {
                ...prevState.newImages,
                [imageType]: prevState.newImages[imageType].filter((_, i) => i !== index)
            }
        }));
    };

    openImageViewer = (imageType, index, isExisting = true) => {
        this.setState({
            activeImageType: imageType,
            currentImageIndex: index,
            isImageViewerOpen: true,
            viewingExistingImages: isExisting
        });
    };

    closeImageViewer = () => {
        this.setState({
            isImageViewerOpen: false,
            activeImageType: null,
            currentImageIndex: 0
        });
    };

    nextImage = () => {
        const { activeImageType, currentImageIndex, viewingExistingImages } = this.state;
        const images = viewingExistingImages
            ? this.state.existingImages[activeImageType]
            : this.state.newImages[activeImageType];

        if (currentImageIndex < images.length - 1) {
            this.setState(prevState => ({
                currentImageIndex: prevState.currentImageIndex + 1
            }));
        }
    };

    prevImage = () => {
        if (this.state.currentImageIndex > 0) {
            this.setState(prevState => ({
                currentImageIndex: prevState.currentImageIndex - 1
            }));
        }
    };

    validateForm = () => {
        const { hallData } = this.state;
        const { existingImages, newImages } = this.state;

        // Verificm dac toate cmpurile obligatorii sunt completate
        if (!hallData.name || !hallData.county || !hallData.city ||
            !hallData.address || !hallData.capacity || !hallData.type || !hallData.tariff) {
            this.setState({ submitError: 'Toate cmpurile marcate cu * sunt obligatorii' });
            return false;
        }

        // Verificm dac avem cel puin o imagine de copert (existent sau nou)
        if (existingImages.cover.length === 0 && newImages.cover.length === 0) {
            this.setState({ submitError: 'Trebuie s existe cel puin o imagine de copert' });
            return false;
        }

        return true;
    };

    prepareFormData = () => {
        const { hallData, newImages, imageIdsToDelete, hallId } = this.state;

        // Asigur-te c valorile numerice sunt numere, nu string-uri
        const processedHallData = {
            ...hallData,
            id: hallId, // Includem explicit ID-ul slii
            capacity: hallData.capacity ? parseInt(hallData.capacity, 10) : 0,
            tariff: hallData.tariff ? parseFloat(hallData.tariff) : 0
        };

        // Crem un FormData pentru a trimite date multipart
        const formData = new FormData();

        // Adugm datele slii ca JSON
        formData.append('sportsHall', JSON.stringify(processedHallData));

        // Adugm IDs imaginilor de ters DOAR dac exist cel puin una
        if (imageIdsToDelete && imageIdsToDelete.length > 0) {
            formData.append('deleteImageIds', JSON.stringify(imageIdsToDelete));
        }

        // Verificm dac exist imagini noi pentru a le aduga
        let hasNewImages = false;
        Object.values(newImages).forEach(imagesList => {
            if (imagesList.length > 0) {
                hasNewImages = true;
            }
        });

        // Adugm imaginile noi i tipurile lor DOAR dac exist cel puin una
        if (hasNewImages) {
            let imageIndex = 0;
            Object.entries(newImages).forEach(([type, imagesList]) => {
                imagesList.forEach(img => {
                    formData.append(`newImages[${imageIndex}]`, img.file);
                    formData.append(`newImageTypes[${imageIndex}]`, type);
                    imageIndex++;
                });
            });
        }

        return formData;
    };

    handleSubmit = async (e) => {
        e.preventDefault();

        // Validm datele
        if (!this.validateForm()) {
            return;
        }

        this.setState({ isSubmitting: true, submitError: null, submitSuccess: false });

        try {
            const formData = this.prepareFormData();
            const token = localStorage.getItem('jwtToken');
            const { hallId } = this.state;

            if (!token) {
                this.setState({
                    isSubmitting: false,
                    submitError: 'Nu suntei autentificat'
                });
                return;
            }

            // Pentru debugging - verific ce conine FormData
            console.log("FormData entries:");
            for (let entry of formData.entries()) {
                console.log(entry[0], entry[1]);
            }

            const response = await fetch(`${this.API_BASE_URL}/sportsHalls/${hallId}`, {
                method: 'PUT',
                body: formData,
                headers: {
                    'Authorization': `Bearer ${token}`
                }
            });

            if (response.ok) {
                const data = await response.json();
                console.log('Hall updated successfully:', data);

                // Salvm datele actualizate direct n stare
                const existingImages = {
                    cover: [],
                    exterior: [],
                    interior: [],
                    locker: []
                };

                if (data.images && data.images.length > 0) {
                    data.images.forEach(img => {
                        const type = img.description;
                        if (existingImages[type]) {
                            existingImages[type].push({
                                id: img.id,
                                url: `${this.API_BASE_URL}/images/${img.id}`,                                type: img.description
                            });
                        }
                    });
                }

                // Actualizm starea cu datele noi i resetm listele de imagini
                this.setState({
                    hallData: {
                        name: data.name || '',
                        county: data.county || '',
                        city: data.city || '',
                        address: data.address || '',
                        capacity: data.capacity || '',
                        type: data.type || '',
                        tariff: data.tariff || '',
                        description: data.description || '',
                        facilities: data.facilities || ''
                    },
                    existingImages,  // Actualizm direct cu imaginile noi
                    imageIdsToDelete: [], // Resetm lista de imagini de ters
                    newImages: {  // Resetm lista de imagini noi
                        cover: [],
                        exterior: [],
                        interior: [],
                        locker: []
                    },
                    isSubmitting: false,
                    submitSuccess: true
                });
            } else {
                // Gestionm eroarea
                const errorText = await response.text();
                console.error('Error updating hall:', errorText);
                console.error('Response status:', response.status);

                this.setState({
                    isSubmitting: false,
                    submitError: `Eroare (${response.status}): ${errorText || 'A aprut o eroare la actualizarea slii'}`
                });
            }
        } catch (error) {
            console.error('Error updating hall:', error);
            this.setState({
                isSubmitting: false,
                submitError: 'Eroare de conexiune la server: ' + error.message
            });
        }
    };

    handleCancel = () => {
        this.props.navigate('/hall-admin-dashboard');
    };

    renderImageSection = () => {
        const { existingImages, newImages } = this.state;

        return (
            <div className="edit-hall-images-section">
                <h2 className="section-title">Imagini</h2>

                <div className="image-categories">
                    {IMAGE_TYPES.map(type => (
                        <div key={type.type} className="image-category">
                            <h3>{type.label} {type.type === 'cover' && '*'}</h3>
                            <p className="image-description">{type.description} (Maxim {type.max})</p>

                            <div className="images-container">
                                {/* Imagini existente */}
                                {existingImages[type.type].map((img, index) => (
                                    <div key={`existing-${index}`} className="image-item">
                                        <div className="image-preview">
                                            <img
                                                src={img.url}
                                                alt={`${type.label} ${index + 1}`}
                                                onClick={() => this.openImageViewer(type.type, index, true)}
                                            />
                                            <button
                                                type="button"
                                                className="delete-image-btn"
                                                onClick={() => this.removeExistingImage(type.type, index)}
                                            >
                                                <Trash2 size={16} />
                                            </button>
                                        </div>
                                    </div>
                                ))}

                                {/* Imagini noi */}
                                {newImages[type.type].map((img, index) => (
                                    <div key={`new-${index}`} className="image-item">
                                        <div className="image-preview">
                                            <img
                                                src={img.preview}
                                                alt={`${type.label} nou ${index + 1}`}
                                                onClick={() => this.openImageViewer(type.type, index, false)}
                                            />
                                            <button
                                                type="button"
                                                className="delete-image-btn"
                                                onClick={() => this.removeNewImage(type.type, index)}
                                            >
                                                <Trash2 size={16} />
                                            </button>
                                        </div>
                                    </div>
                                ))}

                                {/* Buton adugare imagine nou */}
                                {(existingImages[type.type].length + newImages[type.type].length) < type.max && (
                                    <div className="add-image-container">
                                        <label htmlFor={`add-${type.type}`} className="add-image-label">
                                            <ImagePlus size={24} />
                                            <span>Adaug</span>
                                            <input
                                                type="file"
                                                id={`add-${type.type}`}
                                                accept="image/*"
                                                onChange={(e) => this.handleFileChange(e, type.type)}
                                                className="hidden-input"
                                            />
                                        </label>
                                    </div>
                                )}
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        );
    };

    renderImageViewer = () => {
        const {
            isImageViewerOpen,
            activeImageType,
            currentImageIndex,
            viewingExistingImages
        } = this.state;

        if (!isImageViewerOpen || !activeImageType) return null;

        const images = viewingExistingImages
            ? this.state.existingImages[activeImageType]
            : this.state.newImages[activeImageType];

        if (images.length === 0 || currentImageIndex >= images.length) return null;

        const currentImage = images[currentImageIndex];
        const typeConfig = IMAGE_TYPES.find(type => type.type === activeImageType);

        return (
            <div className="image-viewer-overlay" onClick={this.closeImageViewer}>
                <div className="image-viewer-container" onClick={e => e.stopPropagation()}>
                    <div className="image-viewer-header">
                        <h3>{typeConfig.label}</h3>
                        <button className="close-viewer-btn" onClick={this.closeImageViewer}>
                            <X size={24} />
                        </button>
                    </div>

                    <div className="image-viewer-content">
                        <button
                            className={`nav-btn prev ${currentImageIndex === 0 ? 'disabled' : ''}`}
                            onClick={this.prevImage}
                            disabled={currentImageIndex === 0}
                        >
                            <ChevronLeft size={24} />
                        </button>

                        <div className="main-image-container">
                            <img
                                src={viewingExistingImages ? currentImage.url : currentImage.preview}
                                alt={`${typeConfig.label} ${currentImageIndex + 1}`}
                                className="main-image"
                            />
                        </div>

                        <button
                            className={`nav-btn next ${currentImageIndex === images.length - 1 ? 'disabled' : ''}`}
                            onClick={this.nextImage}
                            disabled={currentImageIndex === images.length - 1}
                        >
                            <ChevronRight size={24} />
                        </button>
                    </div>

                    <div className="image-counter">
                        {currentImageIndex + 1} / {images.length}
                    </div>
                </div>
            </div>
        );
    };

    render() {
        const {
            hallData,
            isLoading,
            isSubmitting,
            submitError,
            submitSuccess
        } = this.state;

        if (isLoading) {
            return <div className="loading-container">Se ncarc datele slii...</div>;
        }

        return (
            <div className="edit-hall-page">
                <div className="edit-hall-header">
                    <h1>Editare Sal: {hallData.name}</h1>
                    <button
                        className="back-button"
                        onClick={this.handleCancel}
                    >
                        napoi la Slile Mele
                    </button>
                </div>

                {submitSuccess && (
                    <div className="success-message">
                        Sala a fost actualizat cu succes!
                    </div>
                )}

                {submitError && (
                    <div className="error-message">
                        {submitError}
                    </div>
                )}

                <form onSubmit={this.handleSubmit} className="edit-hall-form">
                    {/* Seciunea de imagini */}
                    {this.renderImageSection()}

                    {/* Seciunea de informaii */}
                    <div className="hall-info-section">
                        <h2 className="section-title">Informaii despre sal</h2>

                        <div className="form-grid">
                            <div className="form-group">
                                <label htmlFor="name">Nume Sal *</label>
                                <input
                                    type="text"
                                    id="name"
                                    name="name"
                                    value={hallData.name}
                                    onChange={this.handleInputChange}
                                    placeholder="Numele slii"
                                    required
                                />
                            </div>

                            <div className="form-group">
                                <label htmlFor="type">Tip Sal *</label>
                                <select
                                    id="type"
                                    name="type"
                                    value={hallData.type}
                                    onChange={this.handleInputChange}
                                    required
                                >
                                    <option value="">Selecteaz tipul slii</option>
                                    <option value="fotbal">Fotbal</option>
                                    <option value="baschet">Baschet</option>
                                    <option value="volei">Volei</option>
                                    <option value="tenis">Tenis</option>
                                    <option value="multipla">Sal Multipl</option>
                                </select>
                            </div>

                            <div className="location-row">
                                <div className="form-group">
                                    <label htmlFor="county">Jude *</label>
                                    <select
                                        id="county"
                                        name="county"
                                        value={hallData.county}
                                        onChange={this.handleInputChange}
                                        required
                                    >
                                        <option value="">Selecteaz Jude</option>
                                        {COUNTIES.map(county => (
                                            <option key={county} value={county}>{county}</option>
                                        ))}
                                    </select>
                                </div>

                                <div className="form-group">
                                    <label htmlFor="city">Ora/Localitate *</label>
                                    <input
                                        type="text"
                                        id="city"
                                        name="city"
                                        value={hallData.city}
                                        onChange={this.handleInputChange}
                                        placeholder="Numele localitii"
                                        required
                                    />
                                </div>
                            </div>

                            <div className="form-group full-width">
                                <label htmlFor="address">Adres *</label>
                                <input
                                    type="text"
                                    id="address"
                                    name="address"
                                    value={hallData.address}
                                    onChange={this.handleInputChange}
                                    placeholder="Adresa complet"
                                    required
                                />
                            </div>

                            <div className="form-group">
                                <label htmlFor="capacity">Capacitate spectatori *</label>
                                <input
                                    type="number"
                                    id="capacity"
                                    name="capacity"
                                    value={hallData.capacity}
                                    onChange={this.handleInputChange}
                                    placeholder="Capacitate maxim tribune"
                                    min="1"
                                    required
                                />
                            </div>

                            <div className="form-group">
                                <label htmlFor="tariff">Tarif (Lei/1h 30min) *</label>
                                <input
                                    type="number"
                                    id="tariff"
                                    name="tariff"
                                    value={hallData.tariff}
                                    onChange={this.handleInputChange}
                                    placeholder="Tarif pe o or i jumtate"
                                    min="0"
                                    step="0.01"
                                    required
                                />
                            </div>

                            <div className="form-group full-width">
                                <label htmlFor="description">Descriere</label>
                                <textarea
                                    id="description"
                                    name="description"
                                    value={hallData.description}
                                    onChange={this.handleInputChange}
                                    placeholder="Adaug o descriere detaliat a slii"
                                    rows="4"
                                />
                            </div>

                            <div className="form-group full-width">
                                <label htmlFor="facilities">Faciliti</label>
                                <textarea
                                    id="facilities"
                                    name="facilities"
                                    value={hallData.facilities}
                                    onChange={this.handleInputChange}
                                    placeholder="Enumer facilitile slii"
                                    rows="3"
                                />
                            </div>
                        </div>
                    </div>

                    <div className="form-actions">
                        <button
                            type="button"
                            className="cancel-button"
                            onClick={this.handleCancel}
                        >
                            Anuleaz
                        </button>
                        <button
                            type="submit"
                            className="save-button"
                            disabled={isSubmitting}
                        >
                            {isSubmitting ? 'Se salveaz...' : 'Salveaz Modificrile'}
                        </button>
                    </div>
                </form>

                {/* Image Viewer Modal */}
                {this.renderImageViewer()}
            </div>
        );
    }
}

export default withRouter(EditHallPage);