                        <p>Nu avei permisiunea s reproducei, distribuii, modificai, creai lucrri derivate, afiai public, efectuai public, republicai, descrcai, stocai sau transmitei orice material de pe Platform, cu excepia cazului n care este permis n mod explicit n aceti Termeni.</p>
