import React from 'react';
import { Link } from 'react-router-dom';
import './PrivacyPolicy.css';
import Footer from './Footer';

const PrivacyPolicy = () => {
    return (
        <div className="privacy-page">
            <header className="privacy-header">
                <div className="privacy-header-content">
                    <Link to="/" className="privacy-logo-link">
                        <div className="privacy-logo-icon"></div>
                        <span className="privacy-logo-text">TrainOn</span>
                    </Link>
                    <Link to="/" className="privacy-back-button">
                        napoi la pagina principal
                    </Link>
                </div>
            </header>

            <main className="privacy-content">
                <div className="privacy-container">
                    <h1 className="privacy-title">Politica de Confidenialitate</h1>
                    <p className="privacy-last-updated">Ultima actualizare: 8 Mai, 2025</p>

                    <section className="privacy-section">
                        <h2>1. Introducere</h2>
                        <p>Bine ai venit la TrainOn. Confidenialitatea datelor dumneavoastr personale este o prioritate pentru noi. Aceast Politic de Confidenialitate explic modul n care colectm, utilizm, divulgm i protejm informaiile personale atunci cnd utilizai platforma noastr de rezervare a slilor de sport.</p>
                        <p>V rugm s citii cu atenie aceast politic pentru a nelege cum tratm datele dumneavoastr.</p>
                    </section>

                    <section className="privacy-section">
                        <h2>2. Informaiile pe care le colectm</h2>
                        <h3>2.1 Informaii personale</h3>
                        <p>Putem colecta urmtoarele informaii personale:</p>
                        <ul>
                            <li>Nume i prenume</li>
                            <li>Adres de email</li>
                            <li>Numr de telefon</li>
                            <li>Data naterii</li>
                            <li>Informaii despre contul dumneavoastr</li>
                            <li>Istoricul rezervrilor</li>
                            <li>Preferinele legate de activitile sportive</li>
                        </ul>

                        <h3>2.2 Informaii colectate automat</h3>
                        <p>Atunci cnd utilizai platforma noastr, anumite informaii sunt colectate automat, inclusiv:</p>
                        <ul>
                            <li>Adresa IP</li>
                            <li>Tipul dispozitivului</li>
                            <li>Tipul i versiunea browserului</li>
                            <li>Sistemul de operare</li>
                            <li>Date despre utilizarea platformei</li>
                            <li>Cookie-uri i tehnologii similare</li>
                        </ul>
                    </section>

                    <section className="privacy-section">
                        <h2>3. Cum utilizm informaiile dumneavoastr</h2>
                        <p>Utilizm informaiile personale colectate pentru:</p>
                        <ul>
                            <li>Facilitarea i procesarea rezervrilor de sli de sport</li>
                            <li>Crearea i gestionarea contului dumneavoastr</li>
                            <li>mbuntirea serviciilor noastre</li>
                            <li>Comunicarea cu dumneavoastr despre rezervri, actualizri sau modificri</li>
                            <li>Trimiterea de notificri legate de serviciile noastre</li>
                            <li>Prevenirea fraudelor i asigurarea securitii platformei</li>
                            <li>Respectarea obligaiilor legale</li>
                        </ul>
                    </section>

                    <section className="privacy-section">
                        <h2>4. Divulgarea informaiilor</h2>
                        <p>Putem divulga informaiile dumneavoastr personale ctre:</p>
                        <ul>
                            <li>Administratorii slilor de sport pentru facilitarea rezervrilor</li>
                            <li>Furnizori de servicii tere care ne ajut s operm platforma</li>
                            <li>Autoriti publice, cnd este cerut de lege</li>
                            <li>Parteneri de afaceri, cu consimmntul dumneavoastr explicit</li>
                        </ul>
                        <p>Nu vom vinde, nchiria sau distribui informaiile dumneavoastr personale ctre teri fr consimmntul dumneavoastr, cu excepia cazurilor prevzute n aceast politic.</p>
                    </section>

                    <section className="privacy-section">
                        <h2>5. Securitatea datelor</h2>
                        <p>Implementm msuri de securitate tehnice i organizaionale pentru a proteja informaiile dumneavoastr personale mpotriva accesului neautorizat, pierderii sau modificrii. Aceste msuri includ criptarea datelor, accesul limitat la informaii i instruirea personalului.</p>
                        <p>Cu toate acestea, nicio metod de transmitere sau stocare electronic nu este 100% sigur. Prin urmare, nu putem garanta securitatea absolut a datelor dumneavoastr.</p>
                    </section>

                    <section className="privacy-section">
                        <h2>6. Drepturile dumneavoastr</h2>
                        <p>n conformitate cu legislaia aplicabil privind protecia datelor, avei urmtoarele drepturi:</p>
                        <ul>
                            <li>Dreptul de acces la datele personale</li>
                            <li>Dreptul la rectificarea datelor inexacte</li>
                            <li>Dreptul la tergerea datelor ("dreptul de a fi uitat")</li>
                            <li>Dreptul la restricionarea prelucrrii</li>
                            <li>Dreptul la portabilitatea datelor</li>
                            <li>Dreptul de a v opune prelucrrii</li>
                            <li>Dreptul de a nu face obiectul unei decizii bazate exclusiv pe prelucrarea automat</li>
                        </ul>
                        <p>Pentru a v exercita aceste drepturi, v rugm s ne contactai folosind informaiile de contact furnizate la sfritul acestei politici.</p>
                    </section>

                    <section className="privacy-section">
                        <h2>7. Cookie-uri</h2>
                        <p>Utilizm cookie-uri i tehnologii similare pentru a mbunti experiena dumneavoastr pe platform. Putei gestiona preferinele pentru cookie-uri prin setrile browserului dumneavoastr. Pentru mai multe informaii despre cookie-urile pe care le folosim, consultai Politica noastr privind Cookie-urile.</p>
                    </section>

                    <section className="privacy-section">
                        <h2>8. Modificri ale Politicii de Confidenialitate</h2>
                        <p>Ne rezervm dreptul de a modifica aceast Politic de Confidenialitate n orice moment. Orice modificri vor fi postate pe aceast pagin, iar data "Ultima actualizare" va fi revizuit. V ncurajm s revizuii periodic aceast politic pentru a fi informat despre cum protejm informaiile dumneavoastr.</p>
                    </section>

                    <section className="privacy-section">
                        <h2>9. Contact</h2>
                        <p>Dac avei ntrebri sau preocupri legate de aceast Politic de Confidenialitate sau de practicile noastre privind datele, v rugm s ne contactai:</p>
                        <div className="privacy-contact-info">
                            <p><strong>Email:</strong> trainon.application@gmail.com</p>
                            <p><strong>Telefon:</strong> +40 354 662 177</p>
                            <p><strong>Adres:</strong> Strada Universitii 7-9, Cluj-Napoca, Romnia</p>
                        </div>
                    </section>
                </div>
            </main>

            <Footer />
        </div>
    );
};

export default PrivacyPolicy;