import React, { useState } from 'react';

const AdminSecuritySettings = ({ userId }) => {
    const [isChangingPassword, setIsChangingPassword] = useState(false);
    const [passwordData, setPasswordData] = useState({
        currentPassword: '',
        newPassword: '',
        confirmPassword: ''
    });
    const [passwordErrors, setPasswordErrors] = useState({});
    const [isLoading, setIsLoading] = useState(false);
    const API_BASE_URL = process.env.REACT_APP_API_URL || '';


    const handleChangePassword = () => {
        console.log('Butonul Schimb parola a fost apsat!');
        console.log('Starea anterioar isChangingPassword:', isChangingPassword);
        setIsChangingPassword(true);
        setPasswordData({
            currentPassword: '',
            newPassword: '',
            confirmPassword: ''
        });
        setPasswordErrors({});
        console.log('Starea setat la isChangingPassword: true');
    };

    const handleCancelPasswordChange = () => {
        setIsChangingPassword(false);
        setPasswordData({
            currentPassword: '',
            newPassword: '',
            confirmPassword: ''
        });
        setPasswordErrors({});
    };

    const handlePasswordInputChange = (field, value) => {
        setPasswordData(prev => ({
            ...prev,
            [field]: value
        }));

        // Clear specific error when user starts typing
        if (passwordErrors[field]) {
            setPasswordErrors(prev => ({
                ...prev,
                [field]: ''
            }));
        }
    };

    const validatePasswordForm = () => {
        const errors = {};

        if (!passwordData.currentPassword) {
            errors.currentPassword = 'Parola curent este obligatorie';
        }

        if (!passwordData.newPassword) {
            errors.newPassword = 'Parola nou este obligatorie';
        } else if (passwordData.newPassword.length < 6) {
            errors.newPassword = 'Parola nou trebuie s aib cel puin 6 caractere';
        }

        if (!passwordData.confirmPassword) {
            errors.confirmPassword = 'Confirmarea parolei este obligatorie';
        } else if (passwordData.newPassword !== passwordData.confirmPassword) {
            errors.confirmPassword = 'Parolele nu se potrivesc';
        }

        if (passwordData.currentPassword === passwordData.newPassword) {
            errors.newPassword = 'Parola nou trebuie s fie diferit de cea curent';
        }

        setPasswordErrors(errors);
        return Object.keys(errors).length === 0;
    };

    const handleSubmitPasswordChange = async () => {
        if (!validatePasswordForm()) {
            return;
        }

        setIsLoading(true);

        try {
            const token = localStorage.getItem('authToken');
            const response = await fetch(`${API_BASE_URL}/users/${userId}/change-password`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${token}`
                },
                body: JSON.stringify({
                    currentPassword: passwordData.currentPassword,
                    newPassword: passwordData.newPassword
                })
            });

            const data = await response.json();

            if (response.ok) {
                alert('Parola a fost schimbat cu succes!');
                setIsChangingPassword(false);
                setPasswordData({
                    currentPassword: '',
                    newPassword: '',
                    confirmPassword: ''
                });
                setPasswordErrors({});
            } else {
                // Handle specific error from backend
                if (data.message) {
                    if (data.message.includes('incorect')) {
                        setPasswordErrors({ currentPassword: data.message });
                    } else {
                        setPasswordErrors({ general: data.message });
                    }
                } else {
                    setPasswordErrors({ general: 'A aprut o eroare la schimbarea parolei' });
                }
            }
        } catch (error) {
            console.error('Error changing password:', error);
            setPasswordErrors({ general: 'A aprut o eroare de conectare' });
        } finally {
            setIsLoading(false);
        }
    };

    const handleTwoFactorAuth = () => {
        // TODO: Implementeaz logica pentru autentificarea n dou pai
        console.log('Configureaz autentificarea n dou pai pentru administrator');
    };

    const handleDeleteAccount = () => {
        // TODO: Implementeaz logica pentru tergerea contului de administrator
        if (window.confirm('Eti sigur c vrei s tergi permanent contul de administrator? Aceast aciune nu poate fi anulat i va afecta toate slile administrate.')) {
            console.log('terge contul de administrator');
            // Aici ar trebui s faci un request ctre backend pentru tergerea contului
        }
    };

    return (
        <div className="admin-profile-section">
            <div className="admin-section-header">
                <h2>Securitate</h2>
            </div>

            <div className="admin-security-section">
                <div className="admin-security-item">
                    <div className="admin-security-info">
                        <h3>Schimb parola</h3>
                        <p>Actualizeaz-i parola pentru a menine contul de administrator securizat</p>
                    </div>

                    {!isChangingPassword ? (
                        <button
                            className="admin-security-button"
                            onClick={handleChangePassword}
                        >
                            Schimb parola
                        </button>
                    ) : (
                        <div style={{
                            display: 'flex',
                            flexDirection: 'column',
                            gap: '1rem',
                            width: '100%',
                            maxWidth: '400px',
                            padding: '1.5rem',
                            background: 'rgba(0, 0, 0, 0.05)',
                            backdropFilter: 'blur(3px)',
                            borderRadius: '8px',
                            border: '1px solid rgba(255, 255, 255, 0.1)'
                        }}>
                            {passwordErrors.general && (
                                <div style={{
                                    color: '#ef4444',
                                    fontSize: '0.9rem',
                                    marginBottom: '1rem',
                                    padding: '0.5rem',
                                    background: 'rgba(239, 68, 68, 0.1)',
                                    borderRadius: '4px',
                                    border: '1px solid rgba(239, 68, 68, 0.3)',
                                    textAlign: 'center',
                                    textShadow: '0 1px 3px rgba(0, 0, 0, 0.5)'
                                }}>
                                    {passwordErrors.general}
                                </div>
                            )}

                            <div style={{ display: 'flex', flexDirection: 'column', gap: '0.5rem' }}>
                                <label style={{
                                    fontSize: '0.9rem',
                                    fontWeight: '500',
                                    color: 'rgba(255, 255, 255, 0.95)',
                                    textShadow: '0 2px 6px rgba(0, 0, 0, 0.6)'
                                }}>Parola curent</label>
                                <input
                                    type="password"
                                    className="admin-edit-input"
                                    value={passwordData.currentPassword}
                                    onChange={(e) => handlePasswordInputChange('currentPassword', e.target.value)}
                                    placeholder="Introdu parola curent"
                                    disabled={isLoading}
                                />
                                {passwordErrors.currentPassword && (
                                    <span style={{
                                        color: '#ef4444',
                                        fontSize: '0.8rem',
                                        display: 'block',
                                        marginTop: '0.25rem',
                                        textShadow: '0 1px 3px rgba(0, 0, 0, 0.5)'
                                    }}>
                                        {passwordErrors.currentPassword}
                                    </span>
                                )}
                            </div>

                            <div style={{ display: 'flex', flexDirection: 'column', gap: '0.5rem' }}>
                                <label style={{
                                    fontSize: '0.9rem',
                                    fontWeight: '500',
                                    color: 'rgba(255, 255, 255, 0.95)',
                                    textShadow: '0 2px 6px rgba(0, 0, 0, 0.6)'
                                }}>Parola nou</label>
                                <input
                                    type="password"
                                    className="admin-edit-input"
                                    value={passwordData.newPassword}
                                    onChange={(e) => handlePasswordInputChange('newPassword', e.target.value)}
                                    placeholder="Introdu parola nou (min. 6 caractere)"
                                    disabled={isLoading}
                                />
                                {passwordErrors.newPassword && (
                                    <span style={{
                                        color: '#ef4444',
                                        fontSize: '0.8rem',
                                        display: 'block',
                                        marginTop: '0.25rem',
                                        textShadow: '0 1px 3px rgba(0, 0, 0, 0.5)'
                                    }}>
                                        {passwordErrors.newPassword}
                                    </span>
                                )}
                            </div>

                            <div style={{ display: 'flex', flexDirection: 'column', gap: '0.5rem' }}>
                                <label style={{
                                    fontSize: '0.9rem',
                                    fontWeight: '500',
                                    color: 'rgba(255, 255, 255, 0.95)',
                                    textShadow: '0 2px 6px rgba(0, 0, 0, 0.6)'
                                }}>Confirm parola nou</label>
                                <input
                                    type="password"
                                    className="admin-edit-input"
                                    value={passwordData.confirmPassword}
                                    onChange={(e) => handlePasswordInputChange('confirmPassword', e.target.value)}
                                    placeholder="Confirm parola nou"
                                    disabled={isLoading}
                                />
                                {passwordErrors.confirmPassword && (
                                    <span style={{
                                        color: '#ef4444',
                                        fontSize: '0.8rem',
                                        display: 'block',
                                        marginTop: '0.25rem',
                                        textShadow: '0 1px 3px rgba(0, 0, 0, 0.5)'
                                    }}>
                                        {passwordErrors.confirmPassword}
                                    </span>
                                )}
                            </div>

                            <div className="admin-edit-actions" style={{ marginTop: '1rem' }}>
                                <button
                                    className="admin-save-button"
                                    onClick={handleSubmitPasswordChange}
                                    disabled={isLoading}
                                    style={isLoading ? { opacity: 0.6, cursor: 'not-allowed' } : {}}
                                >
                                    {isLoading ? 'Se salveaz...' : 'Salveaz parola'}
                                </button>
                                <button
                                    className="admin-cancel-button"
                                    onClick={handleCancelPasswordChange}
                                    disabled={isLoading}
                                    style={isLoading ? { opacity: 0.6, cursor: 'not-allowed' } : {}}
                                >
                                    Anuleaz
                                </button>
                            </div>
                        </div>
                    )}
                </div>

                <div className="admin-security-item">
                    <div className="admin-security-info">
                        <h3>Autentificare n dou pai</h3>
                        <p>Adaug un nivel suplimentar de securitate la contul tu de administrator</p>
                    </div>
                    <button
                        className="admin-security-button"
                        onClick={handleTwoFactorAuth}
                    >
                        Configureaz
                    </button>
                </div>

                <div className="admin-security-item danger">
                    <div className="admin-security-info">
                        <h3>terge contul de administrator</h3>
                        <p>terge permanent contul i toate datele asociate, inclusiv slile administrate</p>
                    </div>
                    <button
                        className="admin-security-button danger"
                        onClick={handleDeleteAccount}
                    >
                        terge contul
                    </button>
                </div>
            </div>
        </div>
    );
};

export default AdminSecuritySettings;